.minefield-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.cell-image {
  width: 80%;
  height: 80%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

.minefield-cell {
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}
